<template>
  <div class="h-screen w-screen p-2 mb-6 text-[12px] overflow-auto">
    <div class="flex justify-center relative w-full">
      <h1 class="font-black text-xs">2513班</h1>
      <van-icon @click="router.go(-1)" name="arrow-left" size="0.5rem" class="absolute! left-0 top-0"></van-icon>
      <span class="text-green-500 text-[14px] absolute right-0 bottom-0">退出班级</span>
    </div>
    <div class="flex flex-col">
      <span class="mb-2">家长信息</span>
      <div class="flex items-center mb-1" v-for="value in familyMessage" :key="value.id">
        <van-icon @click="deletePhone(value.id)" name="clear" size="0.7rem" color="red" class="mr-2"></van-icon>
        <div class="mr-4">
          <div class="flex items-center" @click="show = true">
            <span class="mr-1">{{ value.role }}</span>
            <van-icon name="arrow-down" size="0.5rem"></van-icon>
          </div>
          <van-popup v-model:show="show" position="bottom" style="height: 50%;">
            <van-picker class="h-full!" :columns="family" title="我是学生的" @confirm="onConfirm"
              @cancel="onCancel"></van-picker>
          </van-popup>
        </div>
        <div class="flex flex-col justify-center">
          <span>{{ value.telephone }}</span>
          <span v-if="value.login" class="text-red-500 text-[12px] mb-2 flex justify-end">(当前登录身份)</span>
        </div>

      </div>
    </div>
    <div class="flex items-center justify-center" @click="add">
      <van-icon size="0.7rem" name="add-o" class="mr-0.5" color="green"></van-icon>
      <span class="text-green-500">添加更多家长</span>
    </div>
    <div class="fixed bottom-0 left-0 w-full pb-2 flex justify-center">
      <van-button type="success" class="w-[50%]" @click="save">保存</van-button>
    </div>
  </div>

</template>
<script setup lang='ts'>
import { showConfirmDialog, showToast } from 'vant';
import { familyMessage as FamilyMessage, familyMessageSave as FamilyMessageSave } from '@/request/familyMessage/index'
const router = useRouter()
const show = ref<boolean>(false)
const familyMessage = ref([{
  id: 1,
  role: '家长',
  telephone: '13733114245',
  login: true
}, {
  id: 2,
  role: '爷爷',
  telephone: '19839096597',
  login: false
}])
const family = ref([
  {
    text: '妈妈',
    value: '妈妈',
  },
  {
    text: '爸爸',
    value: '爸爸',
  },
  {
    text: '爷爷',
    value: '爷爷',
  },
  {
    text: '奶奶',
    value: '奶奶',
  },
  {
    text: '外公',
    value: '外公',
  },
  {
    text: '外婆',
    value: '外婆',
  }
])
const onConfirm = () => {
  show.value = false
}
const onCancel = () => {
  show.value = false
}

onMounted(() => {
  FamilyMessage('/api/familyMessage', 'get').then(res => {
    if (res.status == 200) {
      familyMessage.value = res.data.data
    }
  })
})
const add = () => {
  if (familyMessage.value.length == 4) {
    showToast({
      message: '最多只能添加4个家长',
      duration: 1000,
      className: ['text-[12px]']
    })
  }
  else {
    familyMessage.value.push({
      id: familyMessage.value.length + 1,
      role: '妈妈',
      telephone: '13733114245',
      login: false
    })
  }
}
const deletePhone = (id: number) => {
  familyMessage.value.map((item, index) => {
    if (item.id == id) {
      if (id == 1) {
        showConfirmDialog({
          title: '删除',
          // showCancelButton: true,
          message: '删除后将无法使用i家校',
          className: ['text-[12px]', 'p-2', 'rounded', 'text-center']
        }).then(() => {
          familyMessage.value.splice(index, 1)
        }).catch(() => { })
        // familyMessage.value.splice(index, 1)
      }
      else {
        familyMessage.value.splice(index, 1)
      }
    }
  })
}
const save = () => {
  FamilyMessageSave('/api/familyMessage/save', 'put', {
    ...familyMessage.value
  }).then((res) => {
    if (res.status == 200) {
      showToast({
        message: '保存成功',
        duration: 2000,
        className: ['text-[12px]']
      })
      router.push({ name: 'familyPhone' })
    }
  })
}
</script>
